	module.exports = {"data":{"markdownRemark":{"html":"<h2>Definition</h2>\n<p>As per dictionary, abstraction is the quality of dealing with ideas rather than events.</p>\n<p>Likewise in Object-oriented programming, abstraction is a process of hiding the implementation details from the user, only the functionality will be provided to the user. In other words, the user will have the information on what the object does instead of how it does it.</p>\n<p>In Java, abstraction is achieved using Abstract classes and interfaces.</p>\n<h2>Resources</h2>\n<p><a href=\"https://www.tutorialspoint.com/java/java_abstraction.htm\">Tutorials Point - Java Abstract classes and interfaces</a>\n<br />\n<a href=\"https://docs.oracle.com/javase/tutorial/java/IandI/abstract.html\">Java Tutorials - Java Abstract methods and classes</a></p>","fields":{"slug":"/java/data-abstraction/"},"frontmatter":{"title":"Data Abstraction"}}},"pathContext":{"slug":"/java/data-abstraction/"}}

